<template>
    <div>
        <v-container fluid class="fill-height">
            <v-card tile flat>
                <v-row>
                    <v-spacer></v-spacer>
                    <v-col cols="12" class="col-md-5">
                        <v-carousel
                                delimiter-icon="mdi-radiobox-blank"
                                :show-arrows="slider.showArrows"
                                continuous
                                height="70vh"
                        >
                            <v-carousel-item v-for="pic in product.pics"
                                        :key="pic.id"
                                        align="end"
                                        position="bottom"
                            >
                                <v-sheet height="100%">
                                    <v-row class="fill-height">
                                        <v-img :src="pic.source"
                                                position="top center"
                                                height="100%"
                                        ></v-img>
                                    </v-row>
                                </v-sheet>
                            </v-carousel-item>
                        </v-carousel>
                    </v-col>
                    <v-col cols="12" class="col-md-5">
                        <v-card tile flat outlined>
                            <v-card-title class="display-1 font-weight-light">{{product.title}}</v-card-title>
                            <v-card-text class="py-2 grey--text darken-3--text">{{product.description}}</v-card-text>
                            <v-card-text class="text-h6 font-weight-light">: {{priceBeautifier(product.price)}} &#8381;</v-card-text>
                        </v-card>
                        <v-card tile flat outlined>
                            <v-card-subtitle class="py-2">{{productsForMen.sizesAlert}}</v-card-subtitle>
                            <v-card-subtitle class="py-2"><router-link class="black--text" target="_blank" :to="{name:'measurements'}"></router-link>   ,    </v-card-subtitle>

                            <v-form ref="form">
                                <v-container class="px-15" v-if="product.shortcut === 'suit'">
                                    <v-row>
                                        <v-col cols="12" sm="6" md="4" class="my-0 py-0">
                                            <v-text-field label="  ()." type="number" clearable v-model="order.providedMeasurements.arm"
                                            required
                                            :rules="[rules.required]"></v-text-field>
                                        </v-col>
                                        <v-col cols="12" sm="6" md="4" class="my-0 py-0">
                                            <v-text-field label="  ()." type="number" clearable v-model="order.providedMeasurements.shoulder"
                                            required
                                            :rules="[rules.required]"></v-text-field>
                                        </v-col>
                                        <v-col cols="12" sm="6" md="4" class="my-0 py-0">
                                            <v-text-field label="  ()." type="number" clearable v-model="order.providedMeasurements.back"
                                            required
                                            :rules="[rules.required]"></v-text-field>
                                        </v-col>
                                    </v-row>
                                    <v-row class="my-0 py-0">
                                        <v-col cols="12" sm="6" md="4" class="my-0 py-0">
                                            <v-text-field label="  ()." type="number" clearable v-model="order.providedMeasurements.chest"
                                            required
                                            :rules="[rules.required]"></v-text-field>
                                        </v-col>
                                        <v-col cols="12" sm="6" md="4" class="my-0 py-0">
                                            <v-text-field label="  ()." type="number" clearable v-model="order.providedMeasurements.waist"
                                            required
                                            :rules="[rules.required]"></v-text-field>
                                        </v-col>
                                        <v-col cols="12" sm="6" md="4" class="my-0 py-0">
                                            <v-text-field label="  ()." type="number" clearable v-model="order.providedMeasurements.hips"
                                            required
                                            :rules="[rules.required]"></v-text-field>
                                        </v-col>
                                    </v-row>
                                    <v-row>
                                        <v-col cols="12" sm="6" md="6" class="my-0 py-0">
                                            <v-text-field label="   ()." clearable type="number" v-model="order.providedMeasurements.innerSeam"
                                            required
                                            :rules="[rules.required]"></v-text-field>
                                        </v-col>
                                        <v-col cols="12" sm="6" md="6" class="my-0 py-0">
                                            <v-text-field label="   ()." clearable type="number" v-model="order.providedMeasurements.outerSeam"
                                            required
                                            :rules="[rules.required]"></v-text-field>
                                        </v-col>
                                    </v-row>
                                </v-container>
                                <v-container class="px-15" v-else-if="product.shortcut === 'hoodie' || product.shortcut === 'sweatshirt'">
                                    <v-row>
                                        <v-col cols="12" sm="6" md="4" class="my-0 py-0">
                                            <v-text-field label="  ()." type="number" clearable v-model="order.providedMeasurements.arm"
                                            required
                                            :rules="[rules.required]"></v-text-field>
                                        </v-col>
                                        <v-col cols="12" sm="6" md="4" class="my-0 py-0">
                                            <v-text-field label="  ()." type="number" clearable v-model="order.providedMeasurements.shoulder"
                                            required
                                            :rules="[rules.required]"></v-text-field>
                                        </v-col>
                                        <v-col cols="12" sm="6" md="4" class="my-0 py-0">
                                            <v-text-field label="  ()." type="number" clearable v-model="order.providedMeasurements.back"
                                            required
                                            :rules="[rules.required]"></v-text-field>
                                        </v-col>
                                    </v-row>
                                    <v-row class="my-0 py-0">
                                        <v-col cols="12" sm="6" md="4" class="my-0 py-0">
                                            <v-text-field label="  ()." type="number" clearable v-model="order.providedMeasurements.chest"
                                            required
                                            :rules="[rules.required]"></v-text-field>
                                        </v-col>
                                        <v-col cols="12" sm="6" md="4" class="my-0 py-0">
                                            <v-text-field label="  ()." type="number" clearable v-model="order.providedMeasurements.waist"
                                            required
                                            :rules="[rules.required]"></v-text-field>
                                        </v-col>
                                        <v-col cols="12" sm="6" md="4" class="my-0 py-0">
                                            <v-text-field label="  ()." type="number" clearable v-model="order.providedMeasurements.hips"
                                            required
                                            :rules="[rules.required]"></v-text-field>
                                        </v-col>
                                    </v-row>
                                </v-container>
                                <v-container class="px-15" v-else-if="product.shortcut === 'pants' || product.shortcut === 'shorts'">
                                    <v-row>
                                        <v-col cols="12" sm="6" md="4" class="my-0 py-0">
                                            <v-text-field label="  ()." type="number" clearable v-model="order.providedMeasurements.hips"
                                            required
                                            :rules="[rules.required]"></v-text-field>
                                        </v-col>
                                        <v-col cols="12" sm="6" md="4" class="my-0 py-0">
                                            <v-text-field label="   ()." clearable type="number" v-model="order.providedMeasurements.innerSeam"
                                            required
                                            :rules="[rules.required]"></v-text-field>
                                        </v-col>
                                        <v-col cols="12" sm="6" md="4" class="my-0 py-0">
                                            <v-text-field label="   ()." clearable type="number" v-model="order.providedMeasurements.outerSeam"
                                            required
                                            :rules="[rules.required]"></v-text-field>
                                        </v-col>
                                    </v-row>
                                </v-container>
                            </v-form>
                        </v-card>
                        <v-card tile flat outlined class="pb-10">
                            <v-card-title class="font-weight-light"> :
                                <v-card
                                    width="40px"
                                    height="40px"
                                    :color="order.pickedColor"
                                    flat
                                    outlined
                                    class="ml-5 rounded-circle"
                                ></v-card>
                            </v-card-title>
                            <v-container class="px-15">
                                <v-btn
                                    v-for="color in productsForMen.colors"
                                    :key="color"
                                    :color="color"
                                    class="ma-1 pa-0 rounded-circle"
                                    width="30px"
                                    height="30px"
                                    min-width="0"
                                    v-on:click="storePickedColor(color)"
                                >
                                </v-btn>
                            </v-container>
                        </v-card>
                        <v-card tile flat outlined class="pb-10" v-if="product.shortcut === 'suit' || product.shortcut === 'pants'">
                            <v-card-title class="font-weight-light">  :</v-card-title>
                            <v-sheet class="d-inline-flex justify-space-between px-2" width="100%">
                                <v-radio-group row light x-large>
                                    <v-card
                                        tile
                                        width="30%"
                                        class="band ma-1"
                                        v-for="band in productsForMen.bands"
                                        :key="band.id"
                                        data-index
                                        :ripple="ripple"
                                        v-on:click="storePickedBand(band, $event)"
                                    >
                                            <v-img :src="band.source"
                                                width="100%"
                                            >
                                            </v-img>
                                    </v-card>
                                </v-radio-group>
                            </v-sheet>
                        </v-card>
                        <v-card tile flat outlined class="pb-10">
                            <v-card-title class="font-weight-light">  :</v-card-title>
                            <v-sheet class="d-inline-flex justify-space-between px-6" width="100%">
                                <v-radio-group v-model="order.pickedThickness" row >
                                    <v-radio label="   ()" value="0" on-icon="mdi-check-circle-outline"></v-radio>
                                    <v-radio label="   ()" value="1" on-icon="mdi-check-circle-outline"></v-radio>
                                </v-radio-group>
                            </v-sheet>
                        </v-card>
                        <v-btn
                            dark
                            width="100%"
                            height="3em"
                            style="font-size: 1em; margin: 1px 0;"
                            class="font-weight-light"
                            @click="addToCart(product)"
                        >
                              
                        </v-btn>
                    </v-col>
                    <v-spacer></v-spacer>
                </v-row>
            </v-card>
        </v-container>
    </div>
</template>

<script>
    import { mapState } from 'vuex'

    export default {
        props: ['product'],
        data: function() {
            return {
                bandIsPicked: false,
                ripple: false,
                rules: {
                    required: value => !!value || ' '
                },
                order: {
                    pickedColor: '',
                    pickedBandType: {},
                    pickedThickness: null,
                    providedMeasurements: {
                        arm: '',
                        shoulder: '',
                        back: '',
                        chest: '',
                        waist: '',
                        hips: '',
                        innerSeam: '',
                        outerSeam: ''
                    }
                }
            }
        },

        computed: {
            ...mapState(['productsForMen', 'slider'])
        },

        methods: {
            priceBeautifier(num) {
                let numStr = num.toString();
                let reg = new RegExp(/(?<=\d)(?=(\d{3})+(?!\d))/, 'g');
                let result = numStr.replace(reg, '.');
                return result;
            },
            addToCart(prod) {
                if(this.validate()) {
                    let tempProd = Object.assign({}, prod);
                    let tempOrder = JSON.parse(JSON.stringify(this.order));
                    let prodAndParams = Object.assign(tempProd, tempOrder);
                    this.$store.commit('addToCart', prodAndParams);
                    this.resetForms();
                    window.scrollTo(0,0);
                } else {
                    alert('  ');
                }

            },
            storePickedColor(clr) {
                this.order.pickedColor = clr;
            },
            storePickedBand(band, event) {
                let el = event.target;
                let bands = document.querySelectorAll(".band");
                if (band === this.order.pickedBandType) {
                    this.order.pickedBandType = {};
                    this.bandIsPicked = false;
                    el.parentElement.parentElement.classList.remove('active');
                    el.parentElement.parentElement.classList.remove('elevation-20');
                    el.parentElement.parentElement.classList.remove('rounded-circle');
                } else if (this.bandIsPicked === true) {
                    for(let item of bands) {
                        item.classList.remove('active');
                        item.classList.remove('elevation-20');
                        item.classList.remove('rounded-circle');
                    }
                    this.order.pickedBandType = band;
                    this.bandIsPicked = true;
                    el.parentElement.parentElement.classList.add('active');
                    el.parentElement.parentElement.classList.add('elevation-20');
                    el.parentElement.parentElement.classList.add('rounded-circle');
                } else {
                    this.order.pickedBandType = band;
                    this.bandIsPicked = true;
                    el.parentElement.parentElement.classList.add('active');
                    el.parentElement.parentElement.classList.add('elevation-20');
                    el.parentElement.parentElement.classList.add('rounded-circle');

                }
                console.log("Stored band id: " + this.order.pickedBandType + " Any band is chosen: " + this.bandIsPicked)
            },
            resetForms() {
                this.$refs.form.reset();
                this.bandIsPicked = false;
                this.order.pickedColor = '';
                this.order.pickedBandType = {};
                this.order.pickedThickness = null;
                let actives = document.querySelectorAll('.active');
                for (let item of actives) {
                    item.classList.remove('active');
                    item.classList.remove('elevation-20');
                    item.classList.remove('rounded-circle');
                }
            },
            validate() {
                return this.$refs.form.validate();
            }
        }
    }
</script>

<style scoped>
    .band {
        transition:	all 1s;
    }

    .active {
        border: 1px solid #b9b9b9;
        transform: scale(1.05, 1.05);
        z-index: 100;
    }

    .active::before {
        transition:	all 1s;
        background: none;
        transform: scale(1.05, 1.05);
        z-index: 100;
    }
</style>
