                    <v-row no-gutters>
                        <v-col cols="auto">
                            <v-card-title
                                    class="pr-0 pl-2 text-h6 text-sm-h5 text-md-h5 text-lg-h4 font-weight-normal"
                            >
                                 :
                            </v-card-title>
                        </v-col>
                        <v-spacer></v-spacer>
                        <v-col cols="auto">
                            <v-card-title
                                    class="pl-0 pr-2 text-right text-h5 text-sm-h5 text-md-h5 text-lg-h4 font-weight-normal"
                            >
                                {{totalPrice}} &#8381;
                            </v-card-title>
                        </v-col>
                    </v-row>
                </v-card>

                <v-card flat
                        tile
                        class="d-flex align-center justify-start mx-auto my-3 pb-16"
                        max-width="900"
                        >
                        <v-row justify="center">
                            <v-col cols="12" class="ma-0">
                                <v-dialog v-model="dialog" max-width="800">
                                    <template v-slot:activator="{ on, attrs }">
                                        <v-btn
                                            dark
                                            tile
                                            width="100%"
                                            height="3em"
                                            style="font-size: 1em; margin: 1px 0;"
                                            class="font-weight-light px-0"
                                            v-bind="attrs"
                                            v-on="on"
                                        >
                                              
                                        </v-btn>
                                    </template>
                                    <v-card>
                                        <v-card-title>
                                            <span class="headline font-weight-light"> </span>
                                        </v-card-title>
                                        <v-card-text>
                                        <v-container>
                                            <v-row>
                                                <v-col cols="12" sm="6" md="4" class="py-0">
                                                    <v-text-field label="*"
                                                            v-model="userData.firstName"
                                                            required
                                                    ></v-text-field>
                                                </v-col>
                                                <v-col cols="12" sm="6" md="4" class="py-0">
                                                    <v-text-field label=""
                                                            v-model="userData.middleName"
                                                    ></v-text-field>
                                                </v-col>
                                                <v-col cols="12" sm="6" md="4" class="py-0">
                                                    <v-text-field label="*"
                                                            v-model="userData.lastName"
                                                            required
                                                    ></v-text-field>
                                                </v-col>
                                                <v-col cols="12" class="py-0">
                                                    <v-text-field label=" *"
                                                            v-model="userData.address"
                                                            required
                                                    ></v-text-field>
                                                </v-col>
                                                <v-col cols="12">
                                                    <vue-tel-input-vuetify v-model="userData.phone"
                                                                    required
                                                    ></vue-tel-input-vuetify>
                                                </v-col>
                                                <v-col cols="12" sm="6">
                                                    <v-select
                                                        :items="[' ', 'CDEC', '.']"
                                                        label=" *"
                                                        v-model="userData.deliveryType"
                                                        required
                                                    ></v-select>
                                                </v-col>
                                                <v-col cols="12" sm="6">
                                                    <v-select
                                                        :items="['   ']"
                                                        label=" "
                                                        require
                                                    ></v-select>
                                                </v-col>
                                            </v-row>
                                        </v-container>
                                        <small>*   </small>
                                        </v-card-text>
                                            <v-btn dark
                                                tile
                                                width="100%"
                                                style="font-size: 1em; margin: 1px 0;"
                                                class="font-weight-light px-0"
                                                @click="checkout()"
                                            >
                                                 
                                            </v-btn>
                                    </v-card>
                                </v-dialog>
                            </v-col>
                        </v-row>

                </v-card>
            </v-container>


            <v-container fluid v-else>
                <v-card max-width="900"
                        height="50vh"
                        flat
                        tile
                        class="d-flex flex-column align-center justify-center mx-auto"
                >
                    <v-card-title class="px-0 py-5 text-h4 text-lg-h1 text-sm-h2  font-weight-thin text-center">
                          
                    </v-card-title>
                    <v-card-subtitle class="px-0 py-5 text-lg-h5 font-weight-light text-center">
                          <router-link class="black--text" :to="{name:'catalog'}"></router-link>,    -
                    </v-card-subtitle>
                </v-card>
            </v-container>
        </v-container>
    </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'

export default {
    name: 'cart',

    data: function() {
        return {
            currentlyShowing: null,
            show: false,
            dialog: false,
            orderCreated: false,
            userData: {
                firstName: '',
                middleName: '',
                lastName: '',
                address: '',
                deliveryType: '',
                phone: null
            }
        }
    },
    computed: {
        ...mapState(['cart']),
        ...mapActions(['incrementQuantity', 'decrementQuantity']),
        totalPrice() {
            let total = 0;

            for (let item of this.$store.state.cart) {
                total += Number(item.totalPrice);
            }
            return total.toFixed(2);
        }
    },
    methods: {
        removeFromCart(item) {
            this.$store.commit('removeFromCart', item);
        },
        showMoreInfo(index) {
            if (this.show && this.currentlyShowing !== index) {
                this.currentlyShowing = index;
            } else {
                this.currentlyShowing = index;
                return this.show = !this.show;
            }
        },
        increment(item) {
            this.$store.dispatch('increment', item);
        },
        decrement(item) {
            this.$store.dispatch('decrement', item);
        },
        getThicknessName(item) {
            if (item.pickedThickness === 0) {
                return "   ()";
            } else {
                return "   ()";
            }
        },
        checkout() {
            let tempData = Object.assign({}, this.userData);
            this.$store.dispatch('checkout', tempData);
            this.dialog = false;
            this.userData.firstName = '';
            this.userData.middleName = '';
            this.userData.lastName = '';
            this.userData.address = '';
            this.userData.deliveryType = '';
            this.userData.firstName = null;
            this.orderCreated = true;
        }
    }
}
</script>

<style scoped>
.inner-list {
    line-height: 0.5em;
}
</style>
