<script>
import { mapState } from 'vuex'

export default {
    name: 'home',

    data: function() {
        return {

        }
    },

    directives: {
        scroll: {
            inserted: function (el, binding) {
                let f = function (evt) {
                    if (binding.value(evt, el)) {
                        window.removeEventListener('scroll', f)
                    }
                }
                window.addEventListener('scroll', f)
            }
        },
        declared: {
            bind: function(el, binding) {
                let f = function (evt) {
                    if (binding.value(evt, el)) {
                        window.removeEventListener('load', f)
                    }
                }
                window.addEventListener('load', f)
            }
        }
    },

    methods: {
        handleScroll(evt, el) {
            const animItemHeight = el.offsetHeight;
            const animItemOffset = this.offset(el).top;
            const animStart = 4;

            let animItemPoint = window.innerHeight - animItemHeight / animStart;

            if (animItemHeight > window.innerHeight) {
                animItemPoint = window.innerHeight - window.innerHeight / animStart;
            }

            if ((pageYOffset > animItemOffset - animItemPoint) && pageYOffset < (animItemOffset + animItemHeight)) {
                    el.classList.add('_active');
            } else {
                if (!el.classList.contains('_anim-no-hide')) {
                    el.classList.remove('_active');
                }
            }
        },
        offset(el) {
            const rect = el.getBoundingClientRect();
            const scrollLeft = window.pageXOffset || document.documentElement.scrollLeft;
            const scrollTop = window.pageYOfsset || document.documentElement.scrollTop;
            return { top: rect.top + scrollTop, left: rect.left + scrollLeft };
        }
    },

    computed: {
        ...mapState(['slider'])
    }
};
</script>

<style scoped>
._anim-box-bot {
    transform: translate(0px, 120%);
    opacity: 0;
    transition: all 0.8s ease 0s;
}

._anim-box-left-one {
    transform: translate(-120%, 0px);
    opacity: 0;
    transition: all 1s ease 0.2s;
}

._anim-box-right-one {
    transform: translate(120%, 0px);
    opacity: 0;
    transition: all 1s ease 0.5s;
}

._anim-box-left-two {
    transform: translate(-120%, 0px);
    opacity: 0;
    transition: all 1s ease 0.5s;
}

._anim-box-right-two {
    transform: translate(120%, 0px);
    opacity: 0;
    transition: all 1s ease 0.2s;
}

._active {
    transform: translate(0px, 0px);
    opacity: 1;
}

._anim-box-bot_active {
    transform: translate(0px, 0px) scale(1, 1);
    opacity: 1;
}

</style>
