import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../components/Home.vue'
import Catalog from '../components/Catalog.vue'
import ProductMen from '../components/ProductMen.vue'
import ProductWomen from '../components/ProductWomen.vue'
import Measurements from '../components/Measurements.vue'
import About from '../components/About.vue'
import Cart from '../components/Cart.vue'

Vue.use(VueRouter)

const routes = [
    {path: '/', component: Home, name: "home"},
    {path: '/catalog/:sex?', component: Catalog, name: "catalog", children: [
        {path: '/catalog/:sex/:shortcut', component: ProductMen, name: 'product-men'},
        {path: '/catalog/:sex/:shortcut', component: ProductWomen, name: 'product-women'}
    ]},
    {path: '/measurements', component: Measurements, name: "measurements"},
    {path: '/about', component: About, name: "about"},
    {path: '/cart', component: Cart, name: "cart"},
];

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes,
    scrollBehavior(to) {
        if (to.hash) {
            return { selector: to.hash };
        } else {
            return { x: 0, y: 0 };
        }
    }
});

export default router
