    getters: {
        getCartTotal(state) {
            return state.totalItemsInCart;
        }
    },
    mutations: {
        addToCart(state, item) {
            //       

            let found = state.cart.find(function(product) {
                return JSON.stringify(objDeleteParams(product)) == JSON.stringify(objDeleteParams(item));
            })

            if (found) {
                //   -  +1       
                found.quantity++;
                found.totalPrice = found.quantity * found.price;
            } else {
                // ,     ,     -   
                state.cart.push(item);

                Vue.set(item, 'quantity', 1);
                Vue.set(item, 'totalPrice', item.price);
                for(let i = 0; i < state.cart.length; i++) {
                    console.log('' + JSON.stringify(state.cart[i]));
                }
            }
            state.totalItemsInCart++;
		},
        removeFromCart(state, item) {
            let index = state.cart.indexOf(item);

            if (index > -1) {
                let product = state.cart[index];

                state.totalItemsInCart -= product.quantity;
                state.cart.splice(index, 1);
            }
        },
        incrementQuantity(state, item) {
            let index = state.cart.indexOf(item);
            let product = state.cart[index];
            state.totalItemsInCart++;
            product.quantity++;
            product.totalPrice = Number(product.totalPrice) + Number(product.price);
            for(let i = 0; i < state.cart.length; i++) {
                console.log("   " + i + ": " + JSON.stringify(state.cart[i]))
            }
        },
        decrementQuantity(state, item) {
            let index = state.cart.indexOf(item);
            let product = state.cart[index];

            state.totalItemsInCart--;
            product.quantity--;
            product.totalPrice = Number(product.totalPrice) - Number(product.price);

            for(let i = 0; i < state.cart.length; i++) {
                console.log("   " + i + ": " + JSON.stringify(state.cart[i]))
            }
        },
        createNewOrder(state, data) {
            let items = state.cart;
            let order = {
                userInfo: {},
                items: []
            };
            order.userInfo = data;
            order.items = items;
            state.activeOrders.push(order);
            state.cart = [];
            state.totalItemsInCart = 0;
            console.log(state.activeOrders.length)
        }
    },
    actions: {
        increment(context, item) {
            context.commit('incrementQuantity', item);
        },
        decrement(context, item) {
            let index = this.state.cart.indexOf(item);
            let product = this.state.cart[index];

            if(product.quantity > 1) {
                context.commit('decrementQuantity', item);
            } else {
                context.commit('removeFromCart', item);
            }
        },
        checkout(context, data) {
            context.commit('createNewOrder', data);
        }
    },
    plugins: [
        createPersistedState({
            key: "cartItems",
            paths: ["cart", "totalItemsInCart", "activeOrders"]
        })
    ]
